
html,body{height:100%;padding:0;margin:0
}
.octicon{transform:translateY(2px)
}
a{text-decoration:underline;color:#f77d00
}
a:hover{color:#cb6600
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;min-height:100%;padding-top:80px;padding-bottom:80px;margin:auto;max-width:1300px;display:-ms-flexbox;display:flex;-ms-flex-direction:row;-ms-box-orient:horizontal;box-orient:horizontal
}
.flex-stack{-ms-flex:1;-ms-box-flex:1;flex:1
}
.tooltip{font-family:'Avenir', Helvetica, Arial, sans-serif;display:none;opacity:0;transition:opacity .1s;pointer-events:none;padding:15px;font-size:0.95rem;z-index:10000
}
.tooltip .tooltip-content{background:#4d4d4d;color:white;border-radius:6px;padding:10px 10px 10px
}
.tooltip.tooltip-open-transitionend{display:block
}
.tooltip.tooltip-after-open{opacity:1
}
.input-bar{height:150px
}
.divider{margin:2em 0 1em 0;position:relative
}
.divider .line{position:absolute;top:50%;border-top:1px dashed #c1c1c1;z-index:-1;width:100%
}
.divider span{text-transform:uppercase;font-size:0.75rem;color:#c1c1c1;padding:0 5px 0 0;background:#ffffff
}



