
@import url(~vue2-loading-bar/src/css/loading-bar.css);
.loading-bar-parent{height:4px;width:calc(100% - 8px)
}
.LoadingBar--error{background:#ff493e
}
.LoadingBar--thinking{background:#c1c1c1;transition:background 300ms ease-out
}
.LoadingBar-glow{box-shadow:none
}
#loading-bar{position:relative
}
.input-bar{text-align:left;padding-left:40px
}
.input-bar input{width:calc(100% - 30px);height:30px;font-size:1.5rem;padding:10px;border-width:1px;border-style:solid;border-color:#c1c1c1;border-image:initial
}



